'use strict';

import EventEmitter from 'event-emitter.js';

class DReciver {

    constructor(channelName, node) {

        this._target = node;

        this._events = {
            'reciver-over': new EventEmitter,
            'reciver-enter': new EventEmitter,
            'reciver-exit': new EventEmitter,
            'reciver-drop': new EventEmitter,
            'reciver-activate': new EventEmitter,
            'reciver-deactivate': new EventEmitter,
        };

        this._handlers = {
            'dragover': this._setDragOver.bind(this),
            'dragexit': this._setDragExit.bind(this),
            'dragenter': this._setDragEnter.bind(this),
            'drop': this._setDragDrop.bind(this),
        }

        this._cssClass = {
            'over': 'reciver-over',
            'enter': 'reciver-enter',
            'drop': 'reciver-drop',
            'error': 'reciver-error',
            'access': 'reciver-access',
        };

        this.setHandeler();

    }

    setName() { this._channelName; }

    getName() { return this._channelName; }

    setHandeler() {

        let target = this._target;

        let handlers = this._handlers;

        for (var handlerName in handlers)
            target.addEventListener(handlerName, handlers[handlerName], false);

    }

    getTarget() {

        return this._target;

    }

    setActive() {

        let event = this._events['reciver-activate'];

        let cssList = this._cssClass;

        this._target.classList.add(cssList.access);

        event.emit(this._target);

    }

    _canBeDroped(dataTransfer) {

        let current = this._target;
        let cssList = this._cssClass;

        if (!current.classList.contains(cssList.access)) {
            dataTransfer.effectAllowed = 'none';
            return false;
        }
        else {
            dataTransfer.effectAllowed = 'move';
            return true;
        }

    }

    _setDragOver(e) {

        e = e ? e : window.event;

        if (!this._canBeDroped(e.dataTransfer))
            return

        this._target.classList.add(this._cssClass['over']);

        let event = this._events['reciver-over'];

        event.emit(e.target);

        e.preventDefault();

    }

    _setDragExit(e) {

        e = e ? e : window.event;

        if (!this._canBeDroped(e.dataTransfer))
            return

        this._target.classList.remove(this._cssClass['over']);
        this._target.classList.remove(this._cssClass['enter']);

        let event = this._events['reciver-exit'];

        event.emit(e.target);

        e.preventDefault();

    }

    _setDragEnter(e) {

        e = e ? e : window.event;

        if (!this._canBeDroped(e.dataTransfer))
            return

        this._target.classList.add(this._cssClass['enter']);

        let event = this._events['reciver-enter'];

        event.emit(e.target);

        e.preventDefault();

    }

    _setDragDrop(e) {

        e = e ? e : window.event;

        let event = this._events['reciver-drop'];

        let current = this._target;

        var data = JSON.parse(e.dataTransfer.getData('text'));

        event.emit(data, current);

        e.preventDefault();

    }

    cleanState() {

        let event = this._events['reciver-deactivate'];

        let current = this._target;

        for (var key in this._cssClass)
            current.classList.remove(this._cssClass[key]);

        event.emit(this._target);

    }

    on(eventName, handler) {

        let events = this._events;

        if (events[eventName]) {

        let emitter = events[eventName];
        emitter.push(handler);

        }

    }

}

export default DReciver;
