'use strict';

import EventEmitter from 'event-emitter.js';
import DChannel from 'dchannel.js';

class DSender {

    constructor(node) {

        this._node = node;
        this._channels = [];
        this._events = {
            'dstart': new EventEmitter,
            'dend': new EventEmitter,
        };

        this._handlers = {
            'dragstart': this.setDragStart.bind(this),
            'dragend': this.setDragEnd.bind(this),
        };

        this._node.setAttribute('draggable', true);

        this._node.addEventListener('dragstart', this._handlers.dragstart, false);
        this._node.addEventListener('dragend', this._handlers.dragend, false);

        this.init();

    }

    init() {

        let node = this._node;

        if (!node.hasAttribute('data-subscribe'))
            return;

        let subscribeList = node.getAttribute('data-subscribe')
            .split(',')
            .map( ( channelName ) => {

                if (DChannel.channelExsist(channelName))
                    this.addChannel(channelName);

            });

    }

    addChannel(channelName) {

        if (!this._channels.includes(channelName))
            this._channels.push(channelName);

    }

    setDragStart(e) {

        e = e ? e : window.event;

        let event = this._events['dstart'];

        let message;

        if (this._node.hasAttribute('data-message'))
            message = JSON.parse(this._node.getAttribute('data-message'));
        else
            message = {};

        e.dataTransfer.effectAllowed = 'move';

        event.emit(this._node);

        let data = {};

        data['message'] = message;
        data['channels'] = this._channels;

        DChannel.activateChannel(this._channels);

        e.dataTransfer.setData('text/plain', JSON.stringify(data));

    }

    setDragEnd(e) {

        let event = this._events['dend'];

        DChannel.dropEnd(this._channels);

        event.emit();

    }

    on(eventName, handler) {

        let events = this._events;

        if (events[eventName]) {

        let emitter = events[eventName];

        emitter.push(handler);

        }

    }

}

window.DSender = DSender;
