'use strict';

import DSender from 'dsender.js';
import DReciver from 'dreciver.js';

class DChannel {

    constructor() {

        this._pool = {};

        this.init();

    }

    init() {

        let reciverNodeList =
            document.querySelectorAll('[' + 'data-channel-name' + ']');

        reciverNodeList.forEach( ( node ) => {
            this.addChannel(node.getAttribute('data-channel-name'), node);
        } );

    }

    addChannel(channelName, reciverTarget) {

        var channel = this.getChannel(channelName);

        if (channel)
            channel.push(new DReciver(channelName, reciverTarget));
        else
            this._pool[channelName] = [new DReciver(channelName, reciverTarget)];

    }

    getChannel(channelName) {

        if (this.channelExsist(channelName))
            return this._pool[channelName];
        else
            return false;

    }

    channelExsist(channelName) {

        return this._pool.hasOwnProperty(channelName);

    }

    activateChannel(channels) {

        channels.map( ( channelName ) => {

            var reciverList = this.getChannel(channelName);

            if(reciverList)
                reciverList.map( ( reciver ) => { reciver.setActive(); } );

        });

    }

    dropEnd(channels) {

        channels.map( ( channelName ) => {

            let reciverList = this.getChannel(channelName);

            if(reciverList)
                reciverList.map( ( reciver ) => { reciver.cleanState(); } );

        } );

    }

    setHandler(channelList, handlerName, callback) {

        channelList.map( ( channelName ) => {

            let reciverList = this.getChannel(channelName);

            if(reciverList)
                reciverList.map( ( reciver ) => {
                    reciver.on(handlerName, callback);
                } );

        } );

        return this;

    }

}

window.DChannel = new DChannel

export default window.DChannel;
